sl-input::part(base) {
  box-shadow: none;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: .5rem;
  font-family: Helvetica World, sans-serif;
  font-size: 1rem;
  transition: border .2s, box-shadow .2s;
}

sl-input::part(base):focus-within {
  border-color: #385787;
  box-shadow: 0 0 0 2px #3857871a;
}

sl-input.compact::part(base) {
  padding: .4rem .75rem;
  font-size: .95rem;
}

sl-input.error::part(base) {
  border-color: #933023;
  box-shadow: 0 0 0 2px #f003;
}

sl-input::part(label) {
  text-align: left;
  color: #222;
  width: 100%;
  margin-bottom: .5rem;
  font-family: Barlow Semi Condensed, sans-serif;
  font-weight: 600;
  display: block;
}

.form-wrapper {
  background: #fff;
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 400px;
  padding: 20px;
  display: flex;
  box-shadow: 0 2px 8px #0000000d;
}

.form-wrapper form {
  flex-direction: column;
  gap: 15px;
  width: 100%;
  display: flex;
}

.form-wrapper sl-input, .form-wrapper sl-radio-group {
  width: 100%;
  margin-bottom: 1rem;
}

.form-wrapper p {
  text-align: center;
  margin-top: 15px;
}

.form-wrapper a {
  color: #385787;
  text-decoration: none;
}

.form-wrapper a:hover {
  text-decoration: underline;
}

.form-wrapper .radio-label {
  text-align: center;
  margin-bottom: .5rem;
  font-family: Barlow Semi Condensed, sans-serif;
  font-weight: 600;
  display: block;
}

.form-wrapper sl-radio-group::part(base) {
  justify-content: center;
}

.event-card {
  scroll-snap-align: start;
  background: #fff;
  border-radius: 1rem;
  flex-shrink: 0;
  min-width: 400px;
  max-width: 450px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 10px 20px #0000000d;
}

.event-card:hover {
  transform: translateY(-4px);
}

.event-card img {
  object-fit: cover;
  width: 100%;
  height: 250px;
  margin-bottom: 0;
}

.event-card-body {
  padding: 0 2rem .75rem;
}

.event-card-body h3 {
  color: #385787;
  margin-top: 0;
  font-family: Neo Contact, serif;
}

.button-group {
  gap: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  display: flex;
}

.button-group sl-button {
  flex: 1;
  width: 100%;
}

.button-group sl-button::part(base) {
  width: 100%;
}

@media (width <= 768px) {
  .event-card {
    border-radius: .75rem;
    min-width: 280px;
    max-width: 100%;
    box-shadow: 0 6px 16px #0000000d;
  }

  .event-card-body {
    padding: 1.5rem;
  }

  .event-card-body h3 {
    font-size: 2.5rem;
  }

  .event-card-body p {
    font-size: .85rem;
  }

  .button-group {
    flex-direction: column;
    gap: .75rem;
    margin-top: 1rem;
  }

  .button-group sl-button::part(base) {
    padding: .5rem 1.25rem;
    font-size: .95rem;
  }
}

sl-carousel {
  --slide-gap: 20px;
  padding: 4rem;
  display: flex;
}

sl-carousel::part(slide) {
  justify-content: center;
  display: flex;
}

.event-carousel {
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  gap: 2rem;
  padding: 4rem;
  display: flex;
  overflow-x: auto;
}

.event-carousel::-webkit-scrollbar {
  height: 8px;
}

.event-carousel::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 4px;
}

.event-carousel::-webkit-scrollbar-track {
  background: none;
}

@media (width <= 768px) {
  .event-carousel {
    gap: 1rem;
    padding: 3rem 2rem;
  }
}

sl-dialog::part(panel) {
  background: #fff;
  border-radius: 8px;
  max-width: 600px;
  box-shadow: 0 4px 16px #00000026;
}

sl-dialog::part(title) {
  color: #385787;
  font-family: Barlow Semi Condensed, sans-serif;
