  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  background: none;
  border: none;
  border-bottom: 2px solid #0000;
  padding-bottom: .5rem;
  font-family: Barlow Semi Condensed, sans-serif;
  font-size: 1.5rem;
  font-weight: 600;
  transition: all .3s;
}

.tab-bar .tab.active {
