}

.toast {
  color: #fff;
  opacity: 0;
  z-index: 9999;
  background: #333;
  border-radius: 6px;
  max-width: 300px;
  padding: .75rem 1.25rem;
  font-size: .95rem;
  transition: opacity .3s, transform .3s;
  position: fixed;
  top: 3rem;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 2px 10px #00000026;
}

.toast.visible {
  opacity: 1;
}

.toast.success {
  background: #385787;
}

.toast.danger {
  background: #933023;
}

.site-header {
  color: #fff;
  background-image: url("header.51bdf215.png");
